.card-list {
    z-index: 1;
    position: relative;
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
}

.card-list__title {
    width: 712px;
    height: 46px;
    font-weight: 400;
    font-size: 40px;
    line-height: 46px;
    color: #1a1b22;
    margin: 80px 0 0 0;
    padding: 0 0 0 104px;
}

.card-list__grid {
    display: grid;
    grid-template-columns: repeat(3, 400px);
    grid-template-rows: repeat(auto-fill, 576px);
    gap: 16px;
    margin: 64px 0;
    padding: 0 104px;
}

.card-list__button {
    width: 288px;
    height: 64px;
    border: none;
    border-radius: 32px;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    cursor: pointer;
    background-color: #e8e8e8;
    margin: 0 auto 80px auto;
    padding: 0;
}

.card-list__message-group {
    width: 100%;
    min-height: 374px;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    background-color: #F5F6F7;
    padding: 0 auto;
    margin: 0;
}

.card-list__not-found-img{
    width: 82.5px;
    height: 82.5px;
    position: relative;
    padding: 0;
    margin: 86px 0 0 0;
}

.card-list__message-title {
    font-weight: 400;
    font-size: 26px;
    line-height: 30px;
    text-align: center;
    padding: 0;
    margin: 31.5px 0 0 0;
}

.card-list__message {
    width: 100%;
    height: 24px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #1a1b22;
    padding: 0;
    margin: 16px 0 80px 0;
}
